import React from 'react'
import { Link, NavLink } from 'react-router-dom'

import routes from '../routes'
import logo from "../assets/img/icon.png"




export  function AppHeader (){

   const onToggleManu = () => {
        document.body.classList.toggle('manu-open');
    }

        return (
            <header className="app-header flex">
                <div>
                    <Link to={'/'}><img src={logo} style={{width:'100px',height:'100px'}} /></Link>
                </div>
                <i className="fas fa-bars" onClick={()=>onToggleManu()}></i>
                <nav  className="flex">
                    <i className="fas fa-bars" onClick={()=>onToggleManu()}></i>
                    {routes.map(route => <NavLink onClick={()=>onToggleManu()} exact key={route.path} to={route.path}>{route.label}</NavLink>)}
                </nav>
            </header>
        )
}





