import {HomePage} from './pages/home-page.jsx'
import { Projects } from './pages/projects.jsx';
import {AboutUs} from './pages/about-us.jsx'
import { Contect } from './pages/contect.jsx';
const routes = [
    {
        path:'/',
        component: HomePage,
        label: 'Home',
    },
    {
        path:'/projects',
        component: Projects,
        label: 'Projects'
    },
    {
        path:'/about',
        component: AboutUs,
        label: 'My Story'
    },
    {
        path:'/contact',
        component: Contect,
        label: 'Contact'
    }
]

export default routes;