
import { Link } from "react-router-dom"
import img from "../assets/img/self.jpg"
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import RssFeedIcon from '@mui/icons-material/RssFeed';

export  function Info (){
    return (
       <section className="mg-80px">
       <div className="flex justify-center gap-60px info-continer">
        <div className="text-continer">
            <h2>I'm that what you need.</h2>
            <p>Web developer. Content creator. Code expert!</p>
            <p> I'm a fullstack web developer,I am young, skilled with strong social skills. 
                I can take your project to the next level. 
                I thrive on bringing ideas to life and have a reputation 
                for delivering eye-catching results ahead of schedule.</p>
                <p>Check out my work. <span><Link to={'/contact'}>Connect with me.</Link></span></p>
                <p>Let's work together!</p>
        </div>
        <div className="img-continer">
        <img src={img} alt={img}/>
        </div>
       </div>
       <div className=" flex justify-center">
       <div className=" icon-slection-continer">
           <ul className="icon-slection flex ">
           <li><a href="https://linkedin.com/in/avishaietach"><LinkedInIcon className="linkedIn"/></a></li>
           <li><a href="https://github.com/avishaiEtach"><GitHubIcon className="gitHub"/></a></li>
           <li><RssFeedIcon className="rss"/></li>
           </ul>
       </div>
       </div>
       </section>
    )
}