
import * as React from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useState } from 'react';


const theme = createTheme();

export default function SignIn() {

  const [error, setError] = useState(null);

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    let email = data.get('email');
    let name = data.get('name');
    let massge = data.get('textarea');
    if(!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(email)){
      setError((preverror) => (preverror = 'Invalid email address' ))
      return
    }else{
      setError((preverror) => (preverror = null ))
      const  strMail = `https://mail.google.com/mail/?view=cm&fs=1&to=etach89@gmail.com&su= I want to hire you&body=My name is ${name}+%0D%0A+%0D%0A+${massge}+%0D%0A+%0D%0A
      +I would love for you to come back to me at ${email}`
        window.location = strMail
         email = '';
         name = '';
         massge = '';
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <Container  component="div" maxWidth="xs" style={{margin:0 ,marginBottom:'30px'}}>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Box 
                component="form" 
                onSubmit={handleSubmit} 
                noValidate 
                sx={{ mt: 2}}
              >

          <TextField
              margin="normal"
              required
              fullWidth
              id="name"
              label="Name"
              name="name"
              autoComplete="name"
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              error={error ? true : false}
              helperText={error ? error : ''}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="textarea"
              label="Massge"
              type="text"
              id="massge"
              rows={4}
              multiline
              autoComplete="massge"
            />
            <Button
              type="submit"
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Send
            </Button>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}